import { Component, OnInit, Input, ElementRef } from '@angular/core';
import { HomeSlider } from '../../../shared/data/slider';

@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.scss']
})
export class SliderComponent implements OnInit {

  @Input() sliders: any[];
  @Input() class: string;
  @Input() textClass: string;
  @Input() url: string;
  @Input() category: string;
  @Input() buttonText: string;
  @Input() buttonClass: string;

  constructor(private el: ElementRef) { }

  ngOnInit(): void { }

  ngAfterViewInit(): void {
    this.initLazyLoad();
  }

  private initLazyLoad(): void {
    const options = {
      root: null,
      rootMargin: '0px',
      threshold: 0.1
    };

    const observer = new IntersectionObserver((entries, observer) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          const target = entry.target as HTMLElement;
          const imageUrl = target.getAttribute('data-image');
          if (imageUrl) {
            target.style.backgroundImage = `url(${imageUrl})`;
          }
          observer.unobserve(target);
        }
      });
    }, options);

    const slides = this.el.nativeElement.querySelectorAll('.home');

    // Aplica la imagen al primer slider directamente
    const firstSlide = slides[0] as HTMLElement;
    if (firstSlide) {
      const imageUrl = firstSlide.getAttribute('data-image');
      if (imageUrl) {
        firstSlide.style.backgroundImage = `url(${imageUrl})`;
      }
    }

    // Observa el resto de los sliders
    slides.forEach((slide, index) => {
      if (index > 0) {
        observer.observe(slide);
      }
    });
  }


  public HomeSliderConfig: any = HomeSlider;

}
